import logo from './logo.svg';
import React, { Component } from 'react'
import './App.css';




class App extends Component{
  constructor(){
    super();
    this.state = {
      frase : [],
      busqueda : 3,
    };
  
  this.click = this.click.bind(this);
}

  componentDidMount(){
  
    fetch('https://frann1870.pythonanywhere.com/frases')
    .then(response => response.json())
    .then(frases => this.setState({frase: frases}))
    

   }

   
   click(e) {
    this.setState({
     busqueda : Math.floor(Math.random()*99)
    });
  }

   render()  {
     const {frase, busqueda} = this.state;
     console.log(busqueda)
     console.log(this.state.frase.frases)
     const fraseFiltrada = frase.filter(frase => frase.id == busqueda) 
     console.log(fraseFiltrada.frases)
     console.log(fraseFiltrada)
     

    return (
        <div className='container'>
          <div className='row align-items-center' style={{boxShadow: '9px 11px 19px -4px rgba(100,110,120,0.87)',
color:'#000000',borderRadius:'10px', padding : "15vh",marginTop:'10vh', backgroundColor: '#cfffb3', height:'80vh'}}>
            <div className='col align-self-center'>
              {fraseFiltrada.map(fraseFiltrada => <h1 key={fraseFiltrada.Autores}>{fraseFiltrada.frases}</h1>)}
              {fraseFiltrada.map(fraseFiltrada => <h2 key={fraseFiltrada.Autores}>{fraseFiltrada.Autores}</h2>)} 
              <button className='btn' onClick={this.click} style={{backgroundColor:'#EDB183'}}>Nueva frase</button>

            </div>
          
          </div>
        </div>
      );
    }
  }
  
  export default App;
  